'use client';

import { useState, useEffect } from 'react';
import WeatherCard from '@/components/WeatherCard';
import { fetchWeatherData, WeatherData, getBackgroundImage } from '@/lib/weather-api';

export default function Home() {
  const [data, setData] = useState<WeatherData | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [bgImage, setBgImage] = useState<string>('/bg-images/general day.jpg');

  const loadWeatherData = async (cityName: string) => {
    setLoading(true);
    setError(null);
    
    try {
      const weatherData = await fetchWeatherData(cityName);
      setData(weatherData);
      
      // Arkaplan resmini güncelle
      if (weatherData?.current?.condition?.text && weatherData?.location?.localtime) {
        const newBgImage = getBackgroundImage(
          weatherData.current.condition.text,
          weatherData.location.localtime
        );
        setBgImage(newBgImage);
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : 'An error occurred');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    // Load Geneva by default
    loadWeatherData('Geneva');
  }, []);

  return (
    <div 
      className="h-screen w-screen flex items-center justify-center overflow-hidden relative"
      style={{
        backgroundImage: `url('${bgImage}')`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
      }}
    >
      {/* Background Overlay - daha karanlık overlay */}
      <div className="absolute inset-0 bg-gradient-to-b from-black/30 via-black/20 to-black/40"></div>
      
      {/* Weather Card Component - Full Screen */}
      <div className="relative z-10 w-full h-full max-w-md mx-auto">
        {data && (
          <WeatherCard 
            data={data} 
            onCityChange={loadWeatherData}
          />
        )}
      </div>

      {/* Error Message */}
      {error && (
        <div className="fixed top-4 left-1/2 transform -translate-x-1/2 z-20 bg-red-600 text-white px-4 py-2 rounded-lg shadow-lg text-sm">
          {error}
        </div>
      )}

      {/* Loading State */}
      {loading && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-20">
          <div className="bg-white rounded-lg p-4 flex items-center space-x-2">
            <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-blue-600"></div>
            <span className="text-gray-700 text-sm">Loading...</span>
          </div>
        </div>
      )}
    </div>
  );
}